// 引入时间picker
import ruiDatePicker from '@/components/rattenking-dtpicker/rattenking-dtpicker.vue';
import {
	mapState
} from 'vuex';
export default {
	components: {
		ruiDatePicker
	},
	data() {
		return {
			param: {},
			days: '',
			value1: '',
			value2: '',
			maskState: 0, //优惠券面板显示状态
			desc: '', //备注
			name: '', //备注
			tel: '', //备注
			payType: 1, //1微信 2支付宝
			couponList: [{
					title: '新用户专享优惠券',
					price: 5,
				},
				{
					title: '庆五一发一波优惠券',
					price: 10,
				},
				{
					title: '优惠券优惠券优惠券优惠券',
					price: 15,
				}
			],
			addressData: {
				name: '许小星',
				mobile: '13853989563',
				addressName: '金九大道',
				address: '352221199501010521',
				area: '149号',
				default: false,
			}
		}
	},
	onLoad(option) {
		var data = {};
		data = JSON.parse(option.postid)
		this.param = data
		console.log(data)
		this.getDetailOrder(data);
		//商品数据
		// this.param = JSON.parse(option);
	},
	computed: {
		...mapState(['userInfo'])
	},
	methods: {
		// 获取订单详情
		getDetailOrder(data) {
			var params = {
				token: this.userInfo.token,
				orderId: this.param.affiliateConfirmationId,
				hotelId: this.param.hotelId
			}
			// console.log(params)
			uni.request({
				url: this.api + '/api/orderQuery',
				data: params,
				success: (res) => {
					console.log(res)
				}
			});
		},
		//显示优惠券面板
		toggleMask(type) {
			let timer = type === 'show' ? 10 : 300;
			let state = type === 'show' ? 1 : 0;
			this.maskState = 2;
			setTimeout(() => {
				this.maskState = state;
			}, timer)
		},
		numberChange(data) {
			this.number = data.number;
		},
		changePayType(type) {
			this.payType = type;
		},
		submit() {
			uni.redirectTo({
				url: '/pages/money/pay'
			})
		},
		stopPrevent() {},
		bindChange(e) {
			this.value1 = e;
		},
		bindChange1(e) {
			this.value2 = e;
			var startTime = this.value1;
			var endTime = this.value2;
			localStorage.setItem('startTime', JSON.stringify(startTime));
			localStorage.setItem('endTime', JSON.stringify(endTime));
			var curtime = '';
			startTime = new Date(startTime.replace(/-/g, "/"));
			endTime = new Date(endTime.replace(/-/g, "/"));
			curtime = endTime - startTime;
			var time = parseInt(curtime / (1000 * 60 * 60 * 24));
			localStorage.setItem('time', JSON.stringify(time));
			if (time < 1) {
				this.$api.msg('请重新选择日期~');
			} else {
				this.timeShow = true;
				this.timeDifference = time;
				console.log(this.timeDifference + '天');
			}

		},
		bindCancel() {

		}
	}
}
